            link: 'https://www.meetup.com/Munich-Datacademy/events/269907019/',
            description: '<p>Wenn die Unternehmenszentrale mit einer SAP ERP-Lösung arbeitet, ist eine zusätzliche Cloud ERP-Lösung für die Töchter das perfekte System, um unterschiedliche Geschäftsbereiche an die Unternehmens-IT anzubinden.</p> <p>Was geschieht mit der Geschäftstechnologie eines Unternehmens, wenn es beginnt, neue Unternehmen zu erwerben und zu expandieren? Normalerweise versuchen wachsende Unternehmen, alle ihre neuen Tochtergesellschaften, Geschäftsfelder und Regionen in ein allumfassendes ERP zu integrieren. Aber ist das der intelligenteste Weg, um neue Betriebe und Standorte zu integrieren und zu bewirken, dass diese schnell einen Mehrwert schaffen?</p> <p>Tochtergesellschaften anbinden über den 2-Tier-Ansatz: „Kleinere Tochtergesellschaften benötigen oftmals nicht die funktionale Tiefe der Systeme von Unternehmenszentralen“, sagt Thomas Kramer, Leiter für die Implementierung von Cloud ERP, All for One Steeb. Er ist der Ansicht, dass kleine Tochtergesellschaften von den so genannten Tier-1-Systemen in Bezug auf Funktionalität und Kosten sogar überfordert sein könnten. Häufig wird jedoch ein Tier-1 ERP-System als unerlässlich für die Berichterstattung an die Unternehmenszentrale angesehen, so dass es als die einzige praktikable Lösung auf der ganzen Linie erscheint.</p> <p>Es gibt jedoch noch eine weitere Option: Eine schnell einsatzfähige und Cloud-basierte Alternative, die die Antwort auf so manches Gebet sein könnte – eine 2-Tier-Strategie, die sich die Stärken einer schlankeren Cloud-basierten ERP-Lösung zunutze macht. Was verstehen wir unter einem 2-Tier-Ansatz? Hierbei werden zwei ERP-Lösungen gleichzeitig betrieben und miteinander integriert. Im Ergebnis lässt sich die robuste, umfassende ERP-Lösung auf Konzernebene (Tier 1) nutzen, während die flexible, agile und vor allem kostengünstige Lösung auf Ebene der Tochtergesellschaften (Tier 2) zur Verfügung steht.</p> <p>Melden Sie sich an und erfahren Sie mehr über die Vorteile einer 2-Tier-Strategie mit SAP Cloud ERP.</p> <p>Wir freuen uns auf Ihre Teilnahme!<br/>Anmeldung unter:<br/><a href=\'https://bit.ly/3e1K0ZK\' class=\'linkified\'>https://bit.ly/3e1K0ZK</a></p> ',
